<script>
// import Uploadavatar from "@/components/Uploadavatar.vue"
export default {
    data(){
        return {
          islogin:false,
          name:" ",

          }
    },
    mounted(){
      var username=window.localStorage.getItem("users");
      if(username){
        this.islogin=true;
        this.name=JSON.parse(username);
      }

    },
    methods:{
        tologin(){
            this.$router.push({name:'login'});
        },
        toesc(){
         window.localStorage.removeItem("users");
        this.$router.push({name:'login'});
        },
        gotopay(){
            
           this.$router.push({name:'car'});
        },
        gotoadd(){
           this.$router.push({name:'address1'});

        }
    },
    // components:{
    //     Uploadavatar
    // },



}
</script>


